import { types } from "mobx-state-tree"
import { optional } from "mobx-state-tree/dist/internal"


const dataChapter = {
    title: "Chapter1",
    content: "nothing to be written here"
}

export const Category = types.model("Category", {
    type: types.union(types.literal('Action'), types.literal('Fiction'), types.literal('Non-Fiction'))
})

export const Chapter = types.model("Chapter", {
    title: types.string,
    content: types.optional(types.string, "")
})

export const Story = types.model("Story", {
    name: types.string,
    author: types.string,
    createDate: types.Date,
    chapter: types.optional(types.array(Chapter), []),
    category: Category
})
